.profile{
    width: 400px;
    background-color: #1A1B21;
    color: white;
    border-radius: 16px;
    overflow: hidden;
}
.profile-picture{
    width: 100%;
    height: 400px;
    background-image: url(../imgs/profile-pic.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    margin-bottom: 32px;
}
.name{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-bottom: 24px;
}
.profession{
    margin-bottom: 8px;
    color: #F3BF99;
}

p{
    line-height: 32px;
}